.home {
    width: 100%;
    height: 30vh;
    background-color: #25CA46;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home>h3 {
    color: #ffffff;
}

.calendar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
 
.white-container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: auto;
    overflow-x: auto;
}

.white-container>div {
    flex: auto;
}

.TeamContainer {
    display: flex;
    justify-content: center;
    align-content: center;
}

.TeamContainer>div {
    flex: 1;
    height: 30vh;
    margin: 1em;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.red {
    background-color: #FF0000;
}

.white {
    background-color: #F8F8F8;
}

.blue {
    background-color: #0000FF;
}

.green {
    background-color: #00FF00;
}

.teamHeader {
    display: flex;
    padding: 1em;
    justify-content: space-evenly;
    align-items: center;
}

.teamHeader>div {
    padding: 1em;
}

.block {
    padding: 1em;
    background-color: #C4C4C4;
}

.teamName {
    font-weight: 400;
    font-size: 18px;
}

.red>div {
    display: flex;
    padding-left: 1em;
    padding-right: 1em;
    justify-content: space-between;
    align-items: flex-start;
}

.white>div {
    display: flex;
    padding-left: 1em;
    padding-right: 1em;
    justify-content: space-between;
    align-items: flex-start;
}
.blue>div {
    display: flex;
    padding-left: 1em;
    padding-right: 1em;
    justify-content: space-between;
    align-items: flex-start;
}
.green>div {
    display: flex;
    padding-left: 1em;
    padding-right: 1em;
    justify-content: space-between;
    align-items: flex-start;
}

.footer {
    width: 100%;
    height: 16%;
    background-color: #404040;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #ffffff;
}

.footer>div {
    margin: 1em;
    padding: 2em;
}